/* eslint new-cap: 0 */
/* eslint no-debugger: 0 */
/* eslint react/no-find-dom-node:0 */
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Observer from 'react-intersection-observer';
import Random from 'random-id';
import { CustomStep, OptionsStep, TextStep } from './steps';
import schema from './schemas/schema';
import * as storage from './storage';
import ChatBotContainer from './ChatBotContainer';
import Content from './Content';
import Header from './Header';
import HeaderTitle from './HeaderTitle';
import HeaderIcon from './HeaderIcon';
import FloatButton from './FloatButton';
import Footer from './Footer';
import Input from './Input';
import SubmitButton from './SubmitButton';
import Recognition from './recognition';
import {
  ChatIcon, CloseIcon, SubmitIcon, MicIcon,
} from './icons';
import 'intersection-observer';

class ChatBot extends Component {
  /* istanbul ignore next */
  constructor(props) {
    super(props);

    this.state = {
      renderedSteps: [],
      previousSteps: [],
      notRenderedSteps: [],
      currentStep: {},
      previousStep: {},
      steps: {},
      observable: false,
      pendingState: null,
      disabled: true,
      opened: props.opened || !props.floating,
      inputValue: '',
      inputInvalid: false,
      speaking: false,
      recognitionEnable: props.recognitionEnable && Recognition.isSupported(),
      defaultUserSettings: {},
      contentScrollTop: 0,
    };

    this.counter = 0;

    this.renderStep = this.renderStep.bind(this);
    this.getTriggeredStep = this.getTriggeredStep.bind(this);
    this.generateRenderedStepsById = this.generateRenderedStepsById.bind(this);
    this.triggerNextStep = this.triggerNextStep.bind(this);
    this.onValueChange = this.onValueChange.bind(this);
    this.onRecognitionChange = this.onRecognitionChange.bind(this);
    this.onRecognitionEnd = this.onRecognitionEnd.bind(this);
    this.onRecognitionStop = this.onRecognitionStop.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.handleSubmitButton = this.handleSubmitButton.bind(this);
    this.handleContentScroll = this.handleContentScroll.bind(this);
    this.scrollToBottom = this.scrollToBottom.bind(this);
    this.onNodeInserted = this.onNodeInserted.bind(this);
  }

  componentWillMount() {
    const {
      botDelay,
      botAvatar,
      cache,
      cacheName,
      customDelay,
      userAvatar,
      userDelay,
      renderWhenVisible,
    } = this.props;
    const steps = {};
    const { observable } = this.state;

    const defaultBotSettings = { delay: botDelay, avatar: botAvatar };
    const defaultUserSettings = { delay: userDelay, avatar: userAvatar };
    const defaultCustomSettings = { delay: customDelay };

    for (let i = 0, len = this.props.steps.length; i < len; i += 1) {
      const step = this.props.steps[i];
      let settings = {};

      if (step.user) {
        settings = defaultUserSettings;
      } else if (step.message || step.asMessage) {
        settings = defaultBotSettings;
      } else if (step.component) {
        settings = defaultCustomSettings;
      }
      steps[step.id] = Object.assign({}, settings, schema.parse(step));
    }

    schema.checkInvalidIds(steps);

    const firstStep = this.props.steps[0];

    if (firstStep.message) {
      const message = firstStep.message;
      firstStep.message = typeof message === 'function' ? message() : message;
      steps[firstStep.id].message = firstStep.message;
    }

    const {
      currentStep, previousStep, previousSteps, renderedSteps, notRenderedSteps,
    } = storage.getData(
      {
        cacheName,
        cache,
        firstStep,
        steps,
      },
      () => {
        // focus input if last step cached is a user step
        this.setState({ disabled: false }, () => {
          this.input.focus();
        });
      },
    );

    if (renderWhenVisible) {
      if (observable) {
        this.setState({
          currentStep,
          defaultUserSettings,
          previousStep,
          previousSteps,
          renderedSteps,
          notRenderedSteps: notRenderedSteps || [],
          steps,
        });

        if (this.props.handleStepChange) {
          this.props.handleStepChange(currentStep);
        }
      } else {
        this.setState({
          pendingState: {
            currentStep,
            defaultUserSettings,
            previousStep,
            previousSteps,
            renderedSteps,
            notRenderedSteps: notRenderedSteps || [],
            steps,
          },
        });
      }
    } else {
      this.setState({
        currentStep,
        defaultUserSettings,
        previousStep,
        previousSteps,
        renderedSteps,
        notRenderedSteps: notRenderedSteps || [],
        steps,
      });

      if (this.props.handleStepChange) {
        this.props.handleStepChange(currentStep);
      }
    }
  }

  componentDidMount() {
    if (this.state.notRenderedSteps) {
      for (const step of this.state.notRenderedSteps) {
        step.delay = 3000;
        this.triggerNextStep(step);
        this.setState({
          notRenderedSteps: [],
        });
        /*
        TODO: Handle multiple not rendered steps
        */
      }
    }

    const { recognitionEnable } = this.state;
    const { recognitionLang } = this.props;
    if (recognitionEnable) {
      this.recognition = new Recognition(
        this.onRecognitionChange,
        this.onRecognitionEnd,
        this.onRecognitionStop,
        recognitionLang,
      );
    }
    this.content.addEventListener('scroll', this.handleContentScroll);
    this.content.addEventListener('DOMNodeInserted', this.onNodeInserted);
  }

  componentWillUpdate(nextProps, nextState) {
    const { opened } = nextProps;

    if (opened !== undefined && opened !== nextState.opened) {
      this.setState({ opened });
    }
  }

  componentWillUnmount() {
    this.content.removeEventListener('scroll', this.handleContentScroll);
    this.content.removeEventListener('DOMNodeInserted', this.onNodeInserted);
  }

  onNodeInserted(event) {
    this.scrollToBottom(event.currentTarget);
  }

  scrollToBottom(el) {
    const { renderedSteps } = this.state;
    const renderedStepsCount = renderedSteps.length;

    if (renderedStepsCount > this.counter) {
      el.scrollTop = el.scrollHeight + 16;
      this.counter++;
    }
  }

  handleContentScroll(event) {
    const newScrollHeight = Math.ceil(event.currentTarget.scrollTop / 10) * 10;
    if (this.state.contentScrollTop !== newScrollHeight) {
      this.setState({
        contentScrollTop: newScrollHeight,
      });
    }
  }

  onRecognitionChange(value) {
    this.setState({ inputValue: value });
  }

  onRecognitionEnd() {
    this.setState({ speaking: false });
    this.handleSubmitButton();
  }

  onRecognitionStop() {
    this.setState({ speaking: false });
  }

  onValueChange(event) {
    this.setState({ inputValue: event.target.value });
  }

  getTriggeredStep(trigger, value) {
    const steps = this.generateRenderedStepsById();
    return typeof trigger === 'function' ? trigger({ value, steps }) : trigger;
  }

  getStepMessage(message) {
    const { previousSteps } = this.state;
    const lastStepIndex = previousSteps.length > 0 ? previousSteps.length - 1 : 0;
    const steps = this.generateRenderedStepsById();
    const previousValue = previousSteps[lastStepIndex].value;
    return typeof message === 'function' ? message({ previousValue, steps }) : message;
  }

  generateRenderedStepsById() {
    const { previousSteps } = this.state;
    const steps = {};

    for (let i = 0, len = previousSteps.length; i < len; i += 1) {
      const { id, message, value } = previousSteps[i];
      steps[id] = { id, message, value };
    }

    return steps;
  }

  triggerNextStep(data, delayRendering) {
    const {
      defaultUserSettings, previousSteps, renderedSteps, steps,
    } = this.state;
    let { currentStep, previousStep, notRenderedSteps } = this.state;
    const isEnd = currentStep.end;

    if (delayRendering) {
      notRenderedSteps.push(data);
      const { cache, cacheName } = this.props;

      if (cache) {
        setTimeout(() => {
          storage.setData(cacheName, {
            currentStep,
            previousStep,
            previousSteps,
            renderedSteps,
            notRenderedSteps,
          });
        }, 300);
      }
      return;
    }

    if (data && data.value) {
      currentStep.value = data.value;
    }
    if (data && data.trigger) {
      currentStep.trigger = this.getTriggeredStep(data.trigger, data.value);
    }
    // Custom functionality for using external triggers (changing steps) for the ChatBot component
    if (data && data.externalTrigger) {
      const nextStep = Object.assign({}, steps[data.stepId]);
      nextStep.key = Random(24);

      if (data.delay) {
        nextStep.delay = data.delay;
      }

      previousStep = currentStep;
      currentStep = nextStep;

      renderedSteps.push(nextStep);
      previousSteps.push(nextStep);

      this.setState({
        renderedSteps, previousSteps, currentStep, previousStep,
      });
    } else if (isEnd) {
      this.handleEnd();
    } else if (currentStep.options && data) {
      const option = currentStep.options.filter(o => o.value === data.value)[0];
      const trigger = this.getTriggeredStep(option.trigger, currentStep.value);
      delete currentStep.options;

      if (option.callback) {
        option.callback();
      }

      // replace choose option for user message
      currentStep = Object.assign({}, currentStep, option, defaultUserSettings, {
        user: true,
        message: option.label,
        trigger,
      });

      renderedSteps.pop();
      previousSteps.pop();
      renderedSteps.push(currentStep);
      previousSteps.push(currentStep);

      this.setState({
        currentStep,
        renderedSteps,
        previousSteps,
      });
    } else if (currentStep.trigger) {
      if (currentStep.replace) {
        renderedSteps.pop();
      }

      const trigger = this.getTriggeredStep(currentStep.trigger, currentStep.value);
      let nextStep = Object.assign({}, steps[trigger]);

      if (nextStep.message) {
        nextStep.message = this.getStepMessage(nextStep.message);
      } else if (nextStep.update) {
        const updateStep = nextStep;
        nextStep = Object.assign({}, steps[updateStep.update]);

        if (nextStep.options) {
          for (let i = 0, len = nextStep.options.length; i < len; i += 1) {
            nextStep.options[i].trigger = updateStep.trigger;
          }
        } else {
          nextStep.trigger = updateStep.trigger;
        }
      }

      nextStep.key = Random(24);

      previousStep = currentStep;
      currentStep = nextStep;

      this.setState({ renderedSteps, currentStep, previousStep }, () => {
        if (nextStep.user) {
          this.setState({ disabled: false }, () => {
            this.input.focus();
          });
        } else {
          renderedSteps.push(nextStep);
          previousSteps.push(nextStep);

          this.setState({ renderedSteps, previousSteps });
        }
      });
    }

    const { cache, cacheName } = this.props;
    if (cache) {
      setTimeout(() => {
        storage.setData(cacheName, {
          currentStep,
          previousStep,
          previousSteps,
          renderedSteps,
          notRenderedSteps,
        });
      }, 300);
    }

    if (this.props.handleStepChange) {
      this.props.handleStepChange(currentStep);
    }
  }

  handleEnd() {
    if (this.props.handleEnd) {
      const { previousSteps } = this.state;

      const renderedSteps = previousSteps.map((step) => {
        const { id, message, value } = step;
        return { id, message, value };
      });

      const steps = [];

      for (let i = 0, len = previousSteps.length; i < len; i += 1) {
        const { id, message, value } = previousSteps[i];
        steps[id] = { id, message, value };
      }

      const values = previousSteps.filter(step => step.value).map(step => step.value);

      this.props.handleEnd({ renderedSteps, steps, values });
    }
  }

  isLastPosition(step) {
    const { renderedSteps } = this.state;
    const length = renderedSteps.length;
    const stepIndex = renderedSteps.map(s => s.key).indexOf(step.key);

    if (length <= 1 || stepIndex + 1 === length) {
      return true;
    }

    const nextStep = renderedSteps[stepIndex + 1];
    const hasMessage = nextStep.message || nextStep.asMessage;

    if (!hasMessage) {
      return true;
    }

    const isLast = step.user !== nextStep.user;
    return isLast;
  }

  isFirstPosition(step) {
    const { renderedSteps } = this.state;
    const stepIndex = renderedSteps.map(s => s.key).indexOf(step.key);

    if (stepIndex === 0) {
      return true;
    }

    const lastStep = renderedSteps[stepIndex - 1];
    const hasMessage = lastStep.message || lastStep.asMessage;

    if (!hasMessage) {
      return true;
    }

    const isFirst = step.user !== lastStep.user;
    return isFirst;
  }

  handleKeyPress(event) {
    if (event.key === 'Enter') {
      this.submitUserMessage();
    }
  }

  handleSubmitButton() {
    const { inputValue, speaking, recognitionEnable } = this.state;
    if ((_.isEmpty(inputValue) || speaking) && recognitionEnable) {
      this.recognition.speak();
      if (!speaking) {
        this.setState({ speaking: true });
      }
      return;
    }
    this.submitUserMessage();
  }

  submitUserMessage() {
    const {
      defaultUserSettings, inputValue, previousSteps, renderedSteps,
    } = this.state;
    let { currentStep } = this.state;

    const isInvalid = currentStep.validator && this.checkInvalidInput();

    if (!isInvalid) {
      const step = {
        message: inputValue,
        value: inputValue,
      };

      currentStep = Object.assign({}, defaultUserSettings, currentStep, step);

      renderedSteps.push(currentStep);
      previousSteps.push(currentStep);

      this.setState({
        currentStep,
        renderedSteps,
        previousSteps,
        disabled: true,
        inputValue: '',
      });
    }
  }

  checkInvalidInput() {
    const { currentStep, inputValue } = this.state;
    const result = currentStep.validator(inputValue);
    const value = inputValue;

    if (typeof result !== 'boolean' || !result) {
      this.setState(
        {
          inputValue: result.toString(),
          inputInvalid: true,
          disabled: true,
        },
        () => {
          setTimeout(() => {
            this.setState(
              {
                inputValue: value,
                inputInvalid: false,
                disabled: false,
              },
              () => {
                this.input.focus();
              },
            );
          }, 2000);
        },
      );

      return true;
    }

    return false;
  }

  toggleChatBot(opened) {
    if (this.props.toggleFloating) {
      this.props.toggleFloating({ opened });
    } else {
      this.setState({ opened });
    }
  }

  renderStep(step, index) {
    const { renderedSteps } = this.state;
    const {
      avatarStyle,
      bubbleStyle,
      bubbleOptionStyle,
      stepContainerStyle,
      hideBotAvatar,
      hideUserAvatar,
    } = this.props;
    const { options, component, asMessage } = step;
    const steps = this.generateRenderedStepsById();
    const previousStep = index > 0 ? renderedSteps[index - 1] : {};

    if (component && !asMessage) {
      return (
        <CustomStep
          key={index}
          step={step}
          steps={steps}
          style={step.style || stepContainerStyle}
          previousStep={previousStep}
          triggerNextStep={this.triggerNextStep}
          isLast={this.isLastPosition(step)}
        />
      );
    }

    if (options) {
      return (
        <OptionsStep
          key={index}
          step={step}
          style={step.style || stepContainerStyle}
          triggerNextStep={this.triggerNextStep}
          bubbleOptionStyle={bubbleOptionStyle}
          isLast={this.isLastPosition(step)}
        />
      );
    }

    return (
      <TextStep
        key={index}
        step={step}
        steps={steps}
        style={step.style || stepContainerStyle}
        previousStep={previousStep}
        previousValue={previousStep.value}
        triggerNextStep={this.triggerNextStep}
        avatarStyle={avatarStyle}
        bubbleStyle={bubbleStyle}
        hideBotAvatar={hideBotAvatar}
        hideUserAvatar={hideUserAvatar}
        isFirst={this.isFirstPosition(step)}
        isLast={this.isLastPosition(step)}
      />
    );
  }

  render() {
    const {
      disabled,
      inputInvalid,
      inputValue,
      opened,
      renderedSteps,
      speaking,
      recognitionEnable,
    } = this.state;
    const {
      className,
      contentStyle,
      floating,
      footerStyle,
      headerComponent,
      headerTitle,
      hideHeader,
      hideSubmitButton,
      inputStyle,
      placeholder,
      recognitionPlaceholder,
      style,
      submitButtonStyle,
      width,
    } = this.props;

    const header = headerComponent || (
      <Header className="rsc-header">
        <HeaderTitle className="rsc-header-title">{headerTitle}</HeaderTitle>
        {floating && (
          <HeaderIcon className="rsc-header-close-button" onClick={() => this.toggleChatBot(false)}>
            <CloseIcon />
          </HeaderIcon>
        )}
      </Header>
    );

    const icon = (_.isEmpty(inputValue) || speaking) && recognitionEnable ? <MicIcon /> : <SubmitIcon />;

    const inputPlaceholder = speaking ? recognitionPlaceholder : placeholder;

    return (
      <Observer
        // triggerOnce={true}
        onChange={(inView) => {
          if (inView) {
            this.setState(this.state.pendingState);
          }
        }}
        className={`rsc ${className}`}
      >
        {floating && (
          <FloatButton
            className="rsc-float-button"
            opened={opened}
            onClick={() => this.toggleChatBot(true)}
          >
            <ChatIcon />
          </FloatButton>
        )}
        <ChatBotContainer
          className="rsc-container"
          floating={floating}
          opened={opened}
          style={style}
          width={width}
          ref={elem => this.chatBotContainer = elem}
        >
          {!hideHeader && header}
          <Content
            className="rsc-content"
            ref={contentRef => (this.content = contentRef)}
            floating={floating}
            style={contentStyle}
          >
            {_.map(renderedSteps, this.renderStep)}
          </Content>
          <Footer className="rsc-footer" style={footerStyle}>
            <Input
              type="textarea"
              style={inputStyle}
              ref={inputRef => (this.input = inputRef)}
              className="rsc-input"
              placeholder={inputInvalid ? '' : inputPlaceholder}
              onKeyPress={this.handleKeyPress}
              onChange={this.onValueChange}
              value={inputValue}
              floating={floating}
              invalid={inputInvalid}
              disabled={disabled}
              hasButton={!hideSubmitButton}
            />
            {!hideSubmitButton && (
              <SubmitButton
                className="rsc-submit-button"
                style={submitButtonStyle}
                onClick={this.handleSubmitButton}
                invalid={inputInvalid}
                disabled={disabled}
                speaking={speaking}
              >
                {icon}
              </SubmitButton>
            )}
          </Footer>
        </ChatBotContainer>
      </Observer>
    );
  }
}

ChatBot.propTypes = {
  avatarStyle: PropTypes.object,
  botAvatar: PropTypes.string,
  botDelay: PropTypes.number,
  bubbleOptionStyle: PropTypes.object,
  bubbleStyle: PropTypes.object,
  cache: PropTypes.bool,
  cacheName: PropTypes.string,
  className: PropTypes.string,
  contentStyle: PropTypes.object,
  customDelay: PropTypes.number,
  customStyle: PropTypes.object,
  floating: PropTypes.bool,
  footerStyle: PropTypes.object,
  handleEnd: PropTypes.func,
  handleStepChange: PropTypes.func,
  headerComponent: PropTypes.element,
  headerTitle: PropTypes.string,
  hideBotAvatar: PropTypes.bool,
  hideHeader: PropTypes.bool,
  hideSubmitButton: PropTypes.bool,
  hideUserAvatar: PropTypes.bool,
  renderWhenVisible: PropTypes.bool,
  inputStyle: PropTypes.object,
  opened: PropTypes.bool,
  toggleFloating: PropTypes.func,
  placeholder: PropTypes.string,
  recognitionEnable: PropTypes.bool,
  recognitionLang: PropTypes.string,
  recognitionPlaceholder: PropTypes.string,
  steps: PropTypes.array.isRequired,
  stepContainerStyle: PropTypes.object,
  style: PropTypes.object,
  submitButtonStyle: PropTypes.object,
  userAvatar: PropTypes.string,
  userDelay: PropTypes.number,
  width: PropTypes.string,
};

ChatBot.defaultProps = {
  avatarStyle: {},
  botDelay: 1000,
  bubbleOptionStyle: {},
  bubbleStyle: {},
  cache: false,
  cacheName: 'rsc_cache',
  className: '',
  contentStyle: {},
  customStyle: {},
  customDelay: 1000,
  floating: false,
  footerStyle: {},
  handleEnd: undefined,
  handleStepChange: undefined,
  headerComponent: undefined,
  headerTitle: 'Chat',
  hideBotAvatar: false,
  renderWhenVisible: false,
  hideHeader: false,
  hideSubmitButton: false,
  hideUserAvatar: false,
  inputStyle: {},
  opened: undefined,
  placeholder: 'Type the message ...',
  recognitionEnable: false,
  recognitionLang: 'en',
  recognitionPlaceholder: 'Listening ...',
  style: {},
  submitButtonStyle: {},
  toggleFloating: undefined,
  userDelay: 1000,
  width: '100%',
  botAvatar:
    'data:image/svg+xml;base64,',
  userAvatar:
    'data:image/svg+xml;base64,',
};

export default ChatBot;
